:root {
  --table-columns: 2.5fr 1fr 1fr 1fr;
  --blue: #2196f3;
  --table-border-color: 1px solid var(--blue);
}

.cart-table-container {
  display: grid;
  grid-template-columns: 2.5fr 1fr 1fr 1fr;
  grid-template-columns: var(--table-columns);
  max-width: 980px;

  margin: 50px auto;
}

.table-header {
  border-bottom: 1px solid #2196f3;
  border-bottom: var(--table-border-color);
  padding: 15px 0;
}

.table-row {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: 2.5fr 1fr 1fr 1fr;
  grid-template-columns: var(--table-columns);
  border-bottom: 1px solid #2196f3;
  border-bottom: var(--table-border-color);
}


#total-text, #total-price{
  grid-column: 3 / span 1;
  padding: 20px 0;
  font-size: 22px;
}

#total-price {
  grid-column: 4 / span 1;
}

#btn-paypal {
  grid-column: 3 / span 1;
  padding: 20px 0;
}


